/* ===== Media Queries ===== */

@media (max-width: 1200px) {
    .hero-left h1 {
        font-size: 10rem;
        /* Smaller font on large screens */
    }

    .section {
        padding: 8rem 2rem;
    }
}

@media (max-width: 992px) {
    .hero-content {
        grid-template-rows: 1fr;
        padding: 5rem;
        gap: 6rem;
    }

    .hero-right {
        max-width: 40rem;
        margin: 0 auto;
    }

    .hero-left {
        display: flex;
        flex-direction: column;
        padding-right: 0;
    }

    .hero-left h1 {
        display: flex;
        flex-direction: row;
        font-size: 7rem;
    }

    .hero-left .intro {
        max-width: 40rem;
        font-size: 1.6rem;
    }

    .tags-container {
        max-width: 40rem;
        flex-wrap: wrap;
    }

    .tag-bubble {
        font-size: 1.2rem;
        padding: 0.5rem 1rem;
    }

    .profile-icon.top-left {
        top: 0rem;
        left: -1rem;
    }

    .section h2::after {
        left: 50%;
        transform: translateX(-50%);
    }

    .section h2 {
        text-align: center;
        display: block;
    }

    #about p {
        text-align: justify;
        width: 58rem;
        margin: 0 auto;
    }
}

/* Disable hover effects on mobile and tablets */
/* For screens smaller than 768px (tablets and below) */
@media (max-width: 768px) {

    /* Hero Section hover */
    .profile-icon:hover {
        transform: none;
    }

    /* Profile hover effects */
    .hero-right:hover .profile-img {
        transform: none;
    }

    .hero-right:hover .profile-bg-layer.top {
        transform: none;
    }

    .hero-right:hover .profile-bg-layer.bottom {
        transform: none;
    }

    /* Disable animated mouse on mobile */
    /* .animated-mouse {
      display: none;
    } */

    .hero-content {
        grid-template-columns: 1fr;
        gap: 4rem;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .hero-left h1 {
        font-size: 4.5rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        text-align: center;
        gap: 1rem;
    }

    .hero-left .intro {
        font-size: 1.6rem;
        text-align: center;
        margin: 0 auto;
    }

    .profile-icon.top-left {
        top: 0rem;
        left: -1rem;
    }

    .hero-right {
        margin-top: 1rem;
        order: -1;
    }

    .profile-img {
        width: 60%;
        max-width: 30rem;
    }

    .profile-bg-layer {
        width: 80%;
        height: 80%;
    }

    .tags-container {
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
    }

    .menu-toggle {
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 5px;
        width: 30px;
        height: 30px;
        cursor: pointer;
        z-index: 1001;
        transition: transform 0.3s ease;
    }

    .menu-toggle span {
        height: 3px;
        width: 100%;
        background-color: var(--blue);
        border-radius: 2px;
        transition: all 0.4s ease;
    }

    /* Animate into an 'X' when menu is open */
    .menu-toggle.open span:nth-child(1) {
        transform: translateY(8px) rotate(45deg);
    }

    .menu-toggle.open span:nth-child(2) {
        opacity: 0;
    }

    .menu-toggle.open span:nth-child(3) {
        transform: translateY(-8px) rotate(-45deg);
    }

    .nav-links {
        position: absolute;
        top: 6rem;
        right: 0;
        width: 100%;
        background-color: var(--blue);
        font-weight: 800;
        flex-direction: column;
        align-items: center;
        padding: 5rem 1rem;
        gap: 3rem;
        z-index: 999;
        opacity: 0;
        pointer-events: none;
        transition: transform 0.4s ease, opacity 0.4s ease;
    }

    .nav-links.open {
        transform: translateY(0);
        opacity: 1;
        pointer-events: auto;
    }

    .nav-links a {
        color: white;
        box-shadow: var(--shadow);
        border-bottom: 1px solid var(--accent);
        font-size: 1.8rem;
        font-weight: 700;
        text-transform: capitalize;
        transition: color 0.3s ease;
    }

    .section h2 {
        font-size: 2.6rem;
    }

    #about p {
        width: 45rem;
    }

    /* Disable animation hover effects on mobile */
    /* .animate.card:hover,
    .animate.skill-tag:hover,
    .animate.cert-item:hover,
    .animate.contact-item:hover,
    .animate.timeline-content:hover,
    .animate.project-card:hover {
        transform: none;
    } */
}

/* Phones (480px and below) */
@media (max-width: 480px) {
    html {
        font-size: 55%;
    }

    .hero {
        padding: 0;
    }

    .hero-left h1 {
        font-size: 4rem;
    }

    .hero-left .title {
        font-size: 2rem;
    }

    .hero-left .intro {
        font-size: 1.5rem;
        width: 30rem;
    }

    .hero-left h1 {
        display: flex;
        flex-direction: row;
        gap: 1rem;
    }

    .profile-icon.top-left {
        top: -2rem;
        left: -2rem;
    }

    .profile-img {
        width: 70%;
        max-width: 25rem;
    }

    .profile-bg-layer {
        width: 70%;
        height: 70%;
    }

    .tags-container {
        width: 30rem;
        gap: 1rem;
        flex-wrap: wrap;
    }

    .section {
        padding: 6rem 0;
    }

    .card {
        padding: 2rem;
    }

    .timeline-content {
        padding: 2.5rem;
    }

    .project-card {
        padding: 2.5rem;
    }

    .timeline-content ul {
        margin-left: 2rem;
    }

    .github-icon-link {
        transform: scale(0.7);
        top: 1rem;
        right: 1rem;
    }

    #about p {
        max-width: 37rem;
    }
}