/* About Section */
#about {
    padding: 10rem 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-x: hidden;
    width: 100%;
}

#about h2 {
    font-size: 3.2rem;
    font-weight: 700;
    margin-bottom: 4rem;
    color: var(--gray-900);
    display: inline-block;
    position: relative;
}

#about h2::after {
    content: '';
    position: absolute;
    bottom: -8px;
    left: 0;
    width: 6rem;
    height: 3px;
    background: var(--blue);
}

#about p {
    font-size: 1.8rem;
    line-height: 1.7;
    display: flex;
    text-align: justify;
    max-width: 100%;
    width: 100%;
}

@media (max-width: 992px) {
    #about h2::after {
        left: 50%;
        transform: translateX(-50%);
    }

    #about h2 {
        text-align: center;
        display: block;
    }

    #about p {
        text-align: justify;
        width: 58rem;
        margin: 0 auto;
    }
}

@media (max-width: 768px) {
    #about h2 {
        font-size: 2.6rem;
    }

    #about p {
        width: 45rem;
    }
}

@media (max-width: 480px) {
    #about {
        padding: 6rem 0;
    }

    #about p {
        max-width: 37rem;
    }
}