/* Navbar */
.navbar {
    background: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(8px);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 100;
    transition: all var(--transition);
}

.navbar.scrolled {
    box-shadow: var(--shadow);
}

.nav-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 6rem;
    width: 100%;
    max-width: 120rem;
    margin: 0 auto;
    padding: 0 2rem;
}

.nav-logo {
    font-weight: 700;
    font-size: 2.4rem;
    background-color: var(--blue);
    color: white;
    width: 4.5rem;
    height: 4.5rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.8s ease-in-out;
    transform-style: preserve-3d;
    perspective: 1000px;
}

.nav-info {
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav-icons {
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav-icon-link {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-left: 2rem;
    color: var(--blue);
    transition: transform 0.2s ease;
}

.nav-icon-link:hover {
    transform: scale(1.4);
}

.nav-logo:hover {
    background-color: var(--blue);
    color: white;
    transform: rotateY(360deg) rotateX(360deg);
}

.nav-links {
    display: flex;
    list-style: none;
    gap: 3rem;
}

.nav-links a {
    text-decoration: none;
    color: var(--gray-800);
    font-weight: 500;
    font-size: 1.5rem;
    transition: all var(--transition);
    position: relative;
}

.nav-links a::after {
    content: '';
    position: absolute;
    bottom: -3px;
    left: 0;
    width: 100%;
    height: 2px;
    background: var(--blue);
    transform: scaleX(0);
    transform-origin: right;
    transition: transform var(--transition);
}

.nav-links a:hover {
    color: var(--blue);
}

.nav-links a:hover::after {
    transform: scaleX(1);
    transform-origin: left;
}

.menu-toggle {
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 25px;
    height: 20px;
    cursor: pointer;
}

.menu-toggle span {
    height: 2px;
    width: 100%;
    background-color: var(--blue);
    transition: all var(--transition);
}

/* Media Queries for Navbar */
@media (max-width: 768px) {
    .menu-toggle {
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 5px;
        width: 30px;
        height: 30px;
        cursor: pointer;
        z-index: 1001;
        transition: transform 0.3s ease;
    }

    .menu-toggle span {
        height: 3px;
        width: 100%;
        background-color: var(--blue);
        border-radius: 2px;
        transition: all 0.4s ease;
    }

    /* Animate into an 'X' when menu is open */
    .menu-toggle.open span:nth-child(1) {
        transform: translateY(8px) rotate(45deg);
    }

    .menu-toggle.open span:nth-child(2) {
        opacity: 0;
    }

    .menu-toggle.open span:nth-child(3) {
        transform: translateY(-8px) rotate(-45deg);
    }

    .nav-links {
        position: absolute;
        top: 6rem;
        right: 0;
        width: 100%;
        background-color: var(--blue);
        font-weight: 800;
        flex-direction: column;
        align-items: center;
        padding: 5rem 1rem;
        gap: 3rem;
        z-index: 999;
        opacity: 0;
        pointer-events: none;
        transition: transform 0.4s ease, opacity 0.4s ease;
    }

    .nav-links.open {
        transform: translateY(0);
        opacity: 1;
        pointer-events: auto;
    }

    .nav-links a {
        color: white;
        box-shadow: var(--shadow);
        border-bottom: 1px solid var(--accent);
        font-size: 1.8rem;
        font-weight: 700;
        text-transform: capitalize;
        transition: color 0.3s ease;
    }
}